div.gallery {
  display: grid; }
  div.gallery a.cell {
    overflow: hidden;
    position: relative;
    display: block; }
    div.gallery a.cell div.imageWrapper {
      position: relative;
      overflow: hidden; }
      div.gallery a.cell div.imageWrapper:hover::before {
        content: ' ';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: red; }
      div.gallery a.cell div.imageWrapper img.image {
        object-fit: cover; }
